import React from 'react'
import '../../styles/css/Form.css'
import $ from 'jquery'
import { Link } from 'react-router-dom'
import { roundTitle } from '../../styles/jsstyles/main'
class Form extends React.Component {


    constructor(props) {
        super(props)
        this.formSettings = props.formSettings

    }

    send(form) {
        form.preventDefault();
        this.formSettings.submit.action()
    }


    componentDidMount() {

    }
    getBottomSec() {
        if(this.formSettings.bottomSec) {
            return  <Link style={roundTitle(null,'center','crimson','white','16px')} to={this.formSettings.bottomSec.dest}>{this.formSettings.bottomSec.text}</Link>
        }else {
            return null
        }
    }


    getSideButton() {

        if (this.formSettings.sideButton) {
            if(this.formSettings.sideButton.type === 'link' ){
                return (<Link style = {roundTitle(null,'flex-start','crimson','white','12px')} className = 'side-button' to = {this.formSettings.sideButton.to}>{this.formSettings.sideButton.text}</Link>)
            }else return <button  className='side-button' onClick={() => this.formSettings.sideButton.action()}>{this.formSettings.sideButton.text}</button>
         } else
            return null
    }

    getCheckBox() {
        if (this.formSettings.checkbox)
            return (<div style={{ display: 'flex' }}>
                {<input type='checkbox' id={'cb'} onChange={() => {
                    const x = $('cb').attr('checked')
                    this.formSettings.checkbox.action(x)
                }} />}

                <label style={{ fontSize: '14px', marginLeft: '8px', maxLines: '1', width: '180px' }}>{this.formSettings.checkbox.text}</label>

            </div>)
        else
            return null
    }

    getSubTitle() {
        if (this.formSettings.subtitle) {
            return (
                <h3>
                    {this.formSettings.subtitle.text}
                </h3>
            )
        } else {
            return null;
        }
    }

    getSecondInput() {
        if (this.formSettings.inputs.length > 1) {
            return (<div >
                <label>{this.formSettings.inputs[1].title}</label>
                <input className='textBox' type={this.formSettings.inputs[1].type} placeholder={this.formSettings.inputs[1].placeholder} />
            </div>)
        } else return null
    }

    getSubmitButton() {
        if (this.formSettings.submit) {
            return <div style={{ marginTop: '32px' }}>
                <button type='submit' className='form_submit'>{this.formSettings.submit.text}</button>
            </div>
        } else {
            return null
        }
    }

    render() {
        return (
            <form className='form_container' onSubmit={(form) => { this.send(form) }} >
                <h1 className='welcome_text'>
                    {this.formSettings.title}
                </h1>
                {this.getSubTitle()}
                <div>
                    <label>{this.formSettings.inputs[0].title}</label>

                    <input className="textBox" type={this.formSettings.inputs[0].type} placeholder={this.formSettings.inputs[0].placeholder} />
                </div>
                {this.getSecondInput()}
                <div className='form_bottom '>
                    {this.getCheckBox()}
                    {this.getSideButton()}
                </div>
                {this.getSubmitButton()}
                {this.getBottomSec()}
            </form>)

    }
}
export default Form