import { details } from '../../assets/strings';
import '../../styles/css/Home.css';
import { useEffect, useState } from 'react';
import rank from '../../images/rank.png'
import rank2 from '../../images/rank2.png'
import $ from 'jquery'
const Home = (props) => {


    useEffect(() => {
        props.action()
    })

    const slideImages = [
        { url: rank },
        { url: rank2 },
        { url: rank }
    ]
    const Slideshow = (props) => {

        const [currentSlide, setCurrentSlide] = useState(props.slideImages[0].url)
        const [index, setIndex] = useState(0)
        const doChange = () => {
            if (index === props.slideImages.length - 1)
                setIndex(0)
            else setIndex(index + 1)
            setCurrentSlide(props.slideImages[index].url)
            console.log(props.slideImages[index].url)
        }
        useEffect(() => {
            const interval = setInterval(doChange, 2000)
            return () => clearInterval(interval)
        })
        const innerCircle = { background: 'white', borderRadius: '16px', padding: '8px' }
        return <div style={{ display: 'flex', flexDirection: 'column' }}>
            <div style={{ display: 'flex' }}>
                <img alt='' id='home_current_slide' src={currentSlide} style={{ position: 'relative', marginTop: '32px', boxShadow: '0px .1px 10px .2px', width: '400px', height: '300px' }} />

            </div>

            <div style={{ margin: '12px', alignSelf: 'center', width: '20%', borderRadius: '8%', padding: '8px', backgroundColor: 'blue' }}>
                <div style={{ display: 'flex', columnGap: '10%', justifyContent: 'center' }}>
                    <div style={innerCircle} />
                    <div style={innerCircle} />
                    <div style={innerCircle} />

                </div>
            </div>

        </div>
    }
    return (
        <div className='home'>

            <div id='home_main'>

                <div className='left'>
                    <h1><br /></h1>

                </div>

                <div className='content'>

                    <div className='box_container'>

                        <div>
                            <h1 className='text_main_title'> ביקורות </h1>
                            <h2 className='text_main'>{details}</h2>
                            <Slideshow slideImages={slideImages} />
                        </div>

                    </div>
                </div>


                <div></div>
            </div>


            <div className='right'>
                <h1><br /></h1>
            </div>

        </div>
    )
}

export default Home;