import './App.css';
import { Routes } from 'react-router';
import './styles/css/Toolbar.css';
import './styles/css/Logo.css';
import {useState } from 'react';
import { Route } from 'react-router';
import Home from './components/Main/Home';
import Login from './components/Auth/Login';
import Background from './components/Main/Background';
import { Courses } from './components/Courses/Courses';
import CreateAccount from './components/Auth/CreateAccount';
import ForgotPassword from './components/Auth/ForgotPassword';
import Toolbar from './components/Main/Toolbar';
import { TESTING } from './assets/settings';
import { Test } from './components/Test/Test';
import { categories } from './store/courses';
function App() {

  const [backgroundVisible, setBackgroundVisible] = useState(false)

  const [bgTitle, setBgTitle] = useState('לימוד שפות תכנות ופיתוח אפליקציות')
  const [currentCourse, setCurrentCourse] = useState('')
  const [currentContext, setCurrentContext] = useState('')
  function background() {
    if (backgroundVisible) {
      return (<Background title={bgTitle} currentContext={currentContext} currentCourse={currentCourse} />)
    } else return null
  }

 
  const actions = {
    setCourseContext: (context: string) => { setCurrentContext(context) },
    setCourseTitle: (title: string) => { setCurrentCourse(title) },
    setBackground: () => {
      setBgTitle('קורסים')
      setBackgroundVisible(true)
    }
  }


  const page = () => {

    if (TESTING) {
      return <div className='App'><Test /></div>
    } else {
      return (<div className='App'><Toolbar setTitle={(newTitle: string) => { setBgTitle(newTitle); console.log('title') }} />

        {background()}
        <Routes>

          <Route path='/react' element={<Home action={function () {

            setBackgroundVisible(true)
          }} />} />
          <Route path='/Login' element={<Login action={function () {
            setBgTitle('התחברות')
            setBackgroundVisible(true)
          }} />} />
          <Route path='/ForgotPass' element={<ForgotPassword action={function () {
            setBgTitle('שחזור סיסמא')
            setBackgroundVisible(true)
          }} />} />
          <Route path='/CreateAccount' element={<CreateAccount action={function () {
            setBgTitle('משתמש חדש')
            setBackgroundVisible(true)
          }} />} />


          <Route path='/Courses' element={<Courses actions={actions} categories={categories} />} />
        </Routes></div>)
    }
  }
  return page()

}

export default App