import '../../styles/css/Toolbar.css'
import { tree } from '../../assets/pages.js';
import Logo from './Logo';
import '../../App.css'
import { Link } from 'react-router-dom'
function Toolbar(props) {


    function buttons() {
        var buttons = []
        for (var i = 0; i < 3; i++) {
            if (i === 2) {
                buttons.push(<Link onClick={() => { props.setTitle(tree.pages[i - 1]) }} key={`hb_${i}`} className='header_button' to='Login'>{tree.pages[i]}</Link>)
            } else if (i === 0) {
                buttons.push(<Link key={`hb_${i}`} className='header_button' to='Courses'>{tree.pages[i]}</Link>)
            }

        }
        return buttons;
    }

    return (
        // code html
        <div className='App-header'>
            <div className='main'>
                <Logo />
                <div className='buttons_wrapper'>
                    {buttons()}
                </div>

            </div>
        </div>
    )
}

export default Toolbar