import { useEffect, useRef, useState } from 'react'
import { VideoProperties } from './Course'
import ReactPlayer from 'react-player'
import { subTitle } from '../../styles/jsstyles/main'
import '../../utils/i18nextConf';
import { getVideo } from '../../api/videos';

const Video = (props: VideoProperties) => {
    const [videoUrl, setVideoUrl] = useState('')
    const ref = useRef(null)
    useEffect(() => {
        const key = process.env.REACT_APP_VIDEO_API_KEY
        getVideo(props.title, key)
            .then(response => {
                setVideoUrl(response.data)
            })
            .catch(e => console.log(e))
    })
    return (<div>
        <h3 style={subTitle as React.CSSProperties}>הרצאה נוכחית :  {props.title}</h3>
        <span id='video_holder'>
            <ReactPlayer controls url={videoUrl} />
        </span>
        <h4 ref={ref} style={subTitle as React.CSSProperties}>{`נושאים: ${props.description}`}</h4>

        <span>
            {props.extradetails}
        </span>
    </div>)

}
export default Video