export const roundTitle = (line,center,bgColor,textColor,textSize) => {
    const style = {
        backgroundColor: bgColor ? bgColor : '#B1D4E0',
        borderRadius: '8px',
        padding: '8px',
        'marginLeft':'32px',
        'margin':'18px',
        fontSize:textSize ? textSize : '32px',
        'textAlign':'right',
        color:textColor ? textColor : 'black',
        alignSelf:center ? center: 'inherit',
        width: 'fit-content',
        textDecoration: line ? 'underline' : 'none'
    }
    return style
}
export const subTitle = { color: 'black', backgroundColor: 'white', padding: '8px', border: '1px solid black', borderRadius: '4px' ,width:'fit-content','textAlign':'right'}
export const contentPage = (color, noMargin) => {
    const style = {
        flexDirection:'column',
        height: 'fit-content',
        border: '1px solid black',
        minHeight:'100%',
        backgroundColor: color,
        display: 'flex',
        marginTop: noMargin ? '0px' : '32px',
        width: '110%'
    }
    return style
}