
import React from 'react'
import { SectionPropeties, VideoProperties } from './Course';
import { subTitle } from '../../styles/jsstyles/main';
import Video from './Video';
class Section extends React.Component<SectionPropeties, { currentVideo: VideoProperties, videosToggled: boolean, videoIndex: number }> {

    constructor(props: SectionPropeties) {
        super(props)
        this.state = {
            currentVideo: props.sectionVideos[0],
            videosToggled: true,
            videoIndex: 1
        }
    }

    componentDidMount() {
        this.setState({
            currentVideo: this.state.currentVideo ? this.state.currentVideo : this.props.sectionVideos[0],
            videosToggled: this.state.videosToggled,
            videoIndex: 1
        })
    }
    componentDidUpdate() {
        if (this.props.current !== this.state.currentVideo && this.state.videosToggled) {
            this.setState({
                currentVideo: this.props.current!,
                videosToggled: this.state.videosToggled
            })
        }
    }

    render() {
        return (<div className='section_holder'>
            <div style={{ display: 'flex', flexDirection: 'column', rowGap: '0px' }}>
            </div>
            <div id='section_wrapper' >
                <div style={{ display: 'flex', flexDirection: 'column', padding: '8px', minWidth: '300px', alignItems: 'start' }}>
                    <h4 style={subTitle as React.CSSProperties}>הרצאות בפרק זה: </h4>
                    <ul className='sections_tab' id='st_2'>
                        {this.props.sectionVideos.map((sectionVideo, index) => {
                            return (<li key={sectionVideo.title} style={{ fontWeight: sectionVideo.title === this.state.currentVideo.title ? 'bold' : 'normal' }} onClick={() => {
                                this.setState({
                                    currentVideo: this.props.sectionVideos[index],
                                    videosToggled: !this.state.videosToggled,
                                    videoIndex: index + 1
                                })
                            }}>{sectionVideo.title}</li>)

                        })}
                    </ul>

                </div>
                <Video title={this.state.currentVideo.title}
                    description={this.state.currentVideo.description}
                    url={this.state.currentVideo.url}
                    extradetails={this.state.currentVideo.extradetails}></Video>



            </div>



        </div>)
    }
}
export default Section