import React from 'react'
import '../../styles/css/Courses.css'
import './Course'
import Course, { CourseProperties, CourseTransaction } from './Course';
import { CourseCard } from './CourseCard';
import jQuery from "jquery";
import { contentPage, roundTitle } from '../../styles/jsstyles/main';

const $: JQueryStatic = jQuery;
export type CourseCategory = {
    categoryName: string;
    courses: CourseProperties[];
    desc: string;

}
interface AllCourseActions {
    setCourseTitle: (title: string) => void
    setCourseContext: (context: string) => void
    setBackground: () => void
}
type AllCoursesProps = {
    categories: CourseCategory[];
    actions: AllCourseActions;

}
type AllCoursesState = {
    currentCategory: CourseCategory;
    currentCourse: CourseProperties | null;
    elements: CourseProperties[];
    search: string
}
export class Courses extends React.Component<AllCoursesProps, AllCoursesState> implements CourseTransaction {
    constructor(props: AllCoursesProps) {
        super(props)
        this.state = {
            currentCategory: props.categories[0],
            currentCourse: null,
            elements: [],
            search: ''
        }
        this.props.actions.setBackground()
    }
    moveTo = (course: CourseProperties) => {
        $('#search').css('display', 'none')
        $('#courses_area').removeClass('courses_area')
        this.props.actions.setCourseTitle(course.courseName)
        $('#course_header').html(course.courseName)
        this.setState({
            currentCategory: this.state.currentCategory,
            currentCourse: course,
            elements: [],
            search: ''
        })
    }




    filter(search: string) {

        if (search.length === 0) {
            this.setState({
                currentCategory: this.props.categories[0],
                elements: this.state.currentCategory.courses,
                currentCourse: null,
                search: ''
            })
            return;
        }
        const filtered = this.state.currentCategory.courses.filter(course => {
            return (course.courseDescription + course.courseName).toLowerCase().includes(search.toLowerCase())
        })

        this.setState({
            currentCategory: this.props.categories[0],
            currentCourse: null,
            elements: filtered,
            search: ''
        })
    }
    componentWillUnmount() {
        this.props.actions.setCourseTitle('')
    }
    componentDidMount() {
        const elements = this.state.currentCategory.courses.filter(course => {
            if (this.state.search.length > 0)
                return (course.courseDescription + course.courseName).toLowerCase().includes(this.state.search.toLowerCase())
            else
                return true;

        })
        this.setState({
            currentCategory: this.props.categories[0],
            currentCourse: null,
            elements: elements,
            search: ''
        })
    }
    getCourses() {

        if (this.state.currentCourse) {
            return <Course courseName={this.state.currentCourse.courseName}
                coursePrice={this.state.currentCourse.coursePrice}
                courseDescription={this.state.currentCourse.courseDescription}
                courseImage={this.state.currentCourse.courseName}
                courseSections={this.state.currentCourse.courseSections}
                courseActions={{ setCurrentContext: (context) => this.props.actions.setCourseContext(context), moveToCourse: (course) => this.moveTo(course) }}
            />
        } else {
            if(this.state.elements.length === 0) {
                return <div  style ={{padding:'32px'}}>Nothing found</div>
            }else return this.state.elements.map((course, index) => {
                return <CourseCard key={course.courseName + String(index)}
                    courseName={course.courseName}
                    coursePrice={course.coursePrice}
                    courseImage={course.courseImage}
                    courseDescription={course.courseDescription}
                    courseSections={course.courseSections}
                    courseActions={{ moveToCourse: (course: CourseProperties) => this.moveTo(course), setCurrentContext: (context: string) => this.props.actions.setCourseContext(context) }} />
            })
        }

    }


    render() {

        return (
            <div style={contentPage('#0C2D48') as React.CSSProperties} id='courses_page'>
                <br /><br />
                <div id='area-wrapper'>
                    <button style={roundTitle(null, "flex-start", null, "white", '16px') as React.CSSProperties} id='all_courses_btn' onClick={() => {
                        window.location.reload()
                    }}>כל הקורסים</button>

                    <input id='search' type='text' placeholder='חפש'
                        onChange={(event) => { this.filter(event.target.value) }} />

                    <h1 style={roundTitle(null, null, null, "white") as React.CSSProperties} id='course_header'>{this.state.currentCategory.categoryName}</h1>
                    <div id = "courses_area" className='courses_area'>
                        {
                            this.getCourses()
                        }

                    </div>
                </div>
            </div>
        )
    }

}