import axios from "axios"

export async function getVideo(name, apiKey) {

    const req = {
        method: 'GET',
        headers: {
            'Access-Control-Allow-Origin': '*'
        }
    }
    var search = new URLSearchParams()
    search.set('videoName', name + '.mp4')
    search.set('apiKey', apiKey)
    return await axios.get('https://www.nadavsolutions.com/gserver/requestVideo?' + search.toString(), req)
}