import { app_name } from "../../assets/strings";

function Background(props) {
    const context = () => {
        if (props.currentContext) {
            return <p style={{ fontWeight: 'normal', fontSize: '20px', padding: '16px', margin: '0px' }}>{props.currentContext}</p>
        } else {
            return null;
        }
    }
    const currentCourse = () => {
        if (props.title) {
            return <p style={{ fontWeight: 'normal', margin: '8px' }}>{`${props.title} ${props.currentCourse ? ` - ${props.currentCourse}` : ''}`}</p>
        } else {
            return null
        }
    }
    return (
        <header >
            <div style={{ backgroundColor: '#212938', height: '320px', display: 'flex',zIndex:'9', width: '100%', maxWidth: '100%' }}>
                <h1 style={{ color: 'white', padding: '64px', fontSize: '36px' }}>
                    {app_name}<br />
                    <hr />
                    {currentCourse()}
                    {context()}
                </h1>
            </div>
        </header>
    )
}
export default Background