import React from 'react'
import Section from './Section'
//import jQuery from "jquery";
import { subTitle } from '../../styles/jsstyles/main';
//const $: JQueryStatic = jQuery;
/**
 * Course properties ( Entire course means entire page of cattegories  ) 
 */
export interface CourseTransaction {
    moveTo: (course: CourseProperties) => void
}

interface CourseActions {
    moveToCourse: (course: CourseProperties) => void
    setCurrentContext: (context: string) => void;
}
export type CourseProperties = {
    courseName: string;
    coursePrice: string;
    courseImage: string;
    courseDescription: string;
    courseSections: SectionPropeties[];
    courseActions?: CourseActions | null;
};

export type SectionPropeties = {
    current?:VideoProperties | null;
    sectionTitle: string;
    sectionDescription: string;
    sectionVideos: VideoProperties[];
}


/**
 * Used as data for the the Course component
 
 */
export type VideoProperties = {

    title: string | 'undefined';
    description: string | 'undefined';
    url: string | 'undefined';
    extradetails: string | ''; // we do not require this field
}

class Course extends React.Component<CourseProperties, { currentSection: SectionPropeties, sectionToggled: boolean }> {


    constructor(props: CourseProperties) {
        super(props)
        console.log(props)
        this.state = {
            currentSection: this.props.courseSections[0],
            sectionToggled: true
        }

    }
    componentDidMount() {

    }
    componentWillUnmount() {
        this.props.courseActions?.setCurrentContext('')
    }

    render() {
        return (
            <div className='course_holder'>
                <div style={{ display: 'flex', flexDirection: 'column', minWidth: '300px', alignItems: 'start' }}>
                    <h4 style={subTitle as React.CSSProperties}> פרקים </h4>
                    {/* <SlideToggleButton elementId='st_1' time={120} text={this.state.sectionToggled ? 'Collapse' : 'Expand'} onslide={() => this.setState({ currentSection: this.state.currentSection, sectionToggled: !this.state.sectionToggled })} />
                     */}
                    <ol className='sections_tab' id='st_1'>
                        {/*
                        Side bar for the sections
                        */}
                        {this.props.courseSections.map((sectionData, index) => {
                            return <li key={sectionData.sectionTitle} style={{ fontWeight: sectionData.sectionTitle === this.state.currentSection.sectionTitle ? 'bold' : 'normal' }} onClick={() => {
                               
                                this.setState({
                                    currentSection: this.props.courseSections[index],
                                    sectionToggled: this.state.sectionToggled
                                })
                                this.props.courseActions?.setCurrentContext(sectionData.sectionTitle)
                            }}>{sectionData.sectionTitle}</li>
                        })}
                    </ol>

                </div>


                <div className='section_content'>

                    <div className='current_section'>
                        <Section current={this.state.currentSection.sectionVideos[0]} sectionVideos={this.state.currentSection.sectionVideos}
                            sectionDescription={this.state.currentSection.sectionDescription}
                            sectionTitle={this.state.currentSection.sectionTitle} />
                    </div>

                </div>
            </div>

        )
    }
}
export default Course