import { CourseProperties } from "./Course"
import '../..//styles/css/Courses.css'
import React from "react";
import jQuery from "jquery";
type CourseCardState = {
    canAnimate: boolean,
    timer: NodeJS.Timeout | null;
}
const $: JQueryStatic = jQuery;

export class CourseCard extends React.Component<CourseProperties, CourseCardState> {

    constructor(props: CourseProperties) {
        super(props)
        this.state = { canAnimate: true, timer: null }
    }

    componentWillUnmount() {
        if (this.state.timer) {
            clearTimeout(this.state.timer)
        }
    }

    checkCard(element: HTMLElement) {
        var timer =
            setTimeout(() => {
                if (element.style.top !== '10px') {
                    $(element)
                        .css('top', `${10}px`)
                        .css('background-color', 'rgb(244, 244, 244)')
                        .css('box-shadow', 'none')
                }

            }, 1500)
        this.setState({
            canAnimate: this.state.canAnimate, timer: timer
        })

    }

    yBy(element: HTMLElement, enter: boolean) {
        if (!this.state.canAnimate)
            return;

        if (this.state.timer)
            clearTimeout(this.state.timer)

        this.setState({ canAnimate: false })
        let y = 0;
        let yByTimeout = setInterval(() => {
            if (y > 10) {
                clearInterval(yByTimeout)
                this.setState({ canAnimate: true, timer: null })
                this.checkCard(element)
                return;
            }

            $(element).css('top', `${enter ? -y : y}px`)
                .css('box-shadow', enter ? '0px 0px 10px rgb(220, 220, 220)' : 'none')
                .css('background-color', enter ? 'rgb(250,250,250)' : 'rgb(244, 244, 244)')
            y++;
        }, 10)
    }


    render() {
        return (
            <div key={this.props.courseName + this.props.courseDescription} className='course_card'
                onClick={() => {
                    if (this.props.courseActions) {
                        this.props.courseActions.moveToCourse(this.props)
                    }
                }}
                onMouseEnter={(event) => this.yBy(event.currentTarget, true)}
                onMouseLeave={(event) => this.yBy(event.currentTarget, false)}>
                <h3 className='course_card_name'>{this.props.courseName}</h3>
                <h4 className='course_card_price'>{this.props.coursePrice}</h4>
                <img alt='sorry unavailable' className="course_card_image" src={this.props.courseImage}></img>
            </div >)
    }

}
